.header {
  height: 80px;
  background-color: #ffffff;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  position: fixed;
  top: 0;
  left: 280px;
  right: 0;
  z-index: 90;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-circles {
  display: flex;
  gap: 4px;
  position: relative;
}

.circle {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.circle-1 {
  background-color: #9333ea;
}

.circle-2 {
  background-color: #a855f7;
}

.circle-3 {
  background-color: #c084fc;
}

.logo-text {
  display: flex;
  flex-direction: column;
}

.logo-title {
  font-size: 16px;
  font-weight: 700;
  color: #111827;
  line-height: 1.2;
}

.logo-subtitle {
  font-size: 12px;
  color: #6b7280;
  line-height: 1.2;
}

.header-center {
  flex: 1;
  display: flex;
  justify-content: center;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-back-icon {
  font-size: 20px;
  color: #6b7280;
  cursor: pointer;
  transition: color 0.2s;
}

.header-back-icon:hover {
  color: #374151;
}

.header-title h1 {
  font-size: 20px;
  font-weight: 600;
  color: #111827;
  margin: 0;
  line-height: 1.2;
}

.header-title p {
  font-size: 14px;
  color: #6b7280;
  margin: 0;
  line-height: 1.2;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-icon-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #6b7280;
  transition: all 0.2s;
}

.header-icon-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}

.header-profile-menu {
  position: relative;
}

.profile-circle-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #9333ea;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  padding: 0;
}

.profile-circle-btn:hover {
  transform: scale(1.05);
  background-color: #7e22ce;
}

.profile-circle-btn.active {
  background-color: #7e22ce;
}

.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: transparent;
}

.bots-dropdown-menu {
  position: absolute;
  top: calc(100% + 12px);
  right: 0;
  width: 320px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  overflow: hidden;
  animation: slideDown 0.2s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.dropdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e5e7eb;
}

.dropdown-header h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #111827;
}

.close-btn {
  width: 24px;
  height: 24px;
  border: none;
  background: transparent;
  font-size: 24px;
  color: #6b7280;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  transition: color 0.2s;
}

.close-btn:hover {
  color: #111827;
}

.bots-dropdown-list {
  max-height: 400px;
  overflow-y: auto;
  padding: 8px;
}

.bot-menu-item {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border: none;
  background: transparent;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  text-align: left;
  margin-bottom: 4px;
}

.bot-menu-item:hover {
  background-color: #f3f4f6;
}

.bot-menu-item.active {
  background-color: #faf5ff;
  border: 1px solid #e9d5ff;
}

.bot-menu-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.bot-menu-name {
  flex: 1;
  font-size: 14px;
  font-weight: 500;
  color: #111827;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bot-menu-active {
  font-size: 12px;
  font-weight: 600;
  color: #9333ea;
  padding: 2px 8px;
  background-color: #f3e8ff;
  border-radius: 12px;
}

.dropdown-footer {
  padding: 12px;
  border-top: 1px solid #e5e7eb;
}

.btn-create-new-menu {
  width: 100%;
  padding: 10px 16px;
  background-color: #9333ea;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s;
}

.btn-create-new-menu:hover {
  background-color: #7e22ce;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(147, 51, 234, 0.3);
}

.dropdown-loading,
.dropdown-empty {
  padding: 40px 20px;
  text-align: center;
  color: #6b7280;
  font-size: 14px;
}

.dropdown-empty p {
  margin: 0 0 16px 0;
}

.btn-create-in-menu {
  padding: 8px 16px;
  background-color: #9333ea;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-create-in-menu:hover {
  background-color: #7e22ce;
}

